import axios from 'axios';
import React, { useContext, useState } from 'react';
import { Button, Container, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { AuthContext } from '../context/AuthContext';

const SignForm = () => {
  const [email, setEmail] = useState('');
  const [isDuplicate, setIsDuplicate] = useState(false);
  const [message, setMessage] = useState('');

  // const { isLoggedIn } = useContext(AuthContext);

  
  const navigate = useNavigate();

  const changeEmail = (e) => {
    setEmail(e.target.value);
    setIsDuplicate(false);
  }
  const handleSubmit = (e) => {
    e.preventDefault();
    axios({
      url: `/auth/DupCheck.do?email=${email}`, //  `;
      method: 'get', //  
    }).then((res) => {
      if (res.data === false) {
        setIsDuplicate(false);
        if(!email.endsWith('.com')){
          setMessage('     !');
          setIsDuplicate(true);
        } else {
          navigate('/signup');
        }
      } else {
        setMessage('     !');
        setIsDuplicate(true);
      }
    }); 
  };
  return (
    <div>
      <div style={{ marginTop: '30px', fontSize: '30px' }}>   ? <br/>      .</div>
        <Container className="banner-container" style={{ marginTop: '30px' }}>
          <Form onSubmit={handleSubmit} style={{ display: 'flex' }} >
            <Form.Group controlId="email" style={{ marginRight: '10px' }}>
              <Form.Control style={{ backgroundColor: 'rgba(60, 60, 60, 0.5)', color: 'white', width: '400px', height: '50px' }}
                type="email"
                placeholder="  ."
                value={email}
                onChange={changeEmail}
                required isInvalid={isDuplicate}
              />
              {isDuplicate && (
                <Form.Control.Feedback type="invalid" style={{ fontSize: '15px' }}>
                    {message}
                </Form.Control.Feedback>
              )}
            </Form.Group>
            <Button variant="danger" type="submit" style={{height:'50px'}}>
               &gt;
            </Button>
          </Form>
        </Container>
    </div>
  );
};

export default SignForm;